import React, { useContext } from 'react'
import { useState, useEffect } from 'react';
import { AuthContext } from '../../../../context/AuthContext';
import { securedAxiosInstance } from '../../../../axiosConfig';
import toast, { Toaster } from "react-hot-toast";
import { Card, Container, Form } from "@themesberg/react-bootstrap"
import Loader from "../../../../components/Loader";
import FeedbackItem from './FeedbackItem';

function MyFeedbacks() {
    const { state } = useContext(AuthContext);
    const [feedbacks, setFeedbacks] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [showResolved, setshowResolved] = useState(false);

    const fetchMyFeedbacks = async () => {
        securedAxiosInstance
            .get(`/feedbacks?_where[0][student_id]=${state.userDetails.student}&_sort=createdAt:desc`)
            .then((response) => {
                setFeedbacks(response.data)
                setIsLoading(false)
            })
            .catch((error) => {
                setIsLoading(false)
                console.log(error, error.response);
                toast.error("Something Went Wrong!")
            })
    }

    const fetchMyResolvedFeedbacks = async () => {
        securedAxiosInstance
            .get(`/feedbacks?_where[0][isResolved]=true&_where[1][student_id]=${state.userDetails.student}&_sort=createdAt:desc`)
            .then((response) => {
                setFeedbacks(response.data)
                setIsLoading(false)
            })
            .catch((error) => {
                setIsLoading(false)
                console.log(error, error.response);
                toast.error("Something Went Wrong!")
            })
    }

    useEffect(() => {
        setIsLoading(true)
        if(showResolved){
            fetchMyResolvedFeedbacks();
        } else {
            fetchMyFeedbacks();
        }

        // eslint-disable-next-line
    }, [showResolved])

    if (isLoading) return <Loader />; 

  return (
    <Container className="p-4">
        <Toaster />
        <Card className="p-4">
            <div className="d-flex justify-content-between align-items-center mb-3">
            <div className="text-center flex-grow-1">
                <h3 className="mb-0">My Feedbacks Or Queries</h3>
            </div>
            <div>
                <Form.Check
                type="switch"
                id="custom-switch"
                label="Show Resolved"
                className="mb-0"
                checked={showResolved}
                onChange={() => setshowResolved(!showResolved)}
                />
            </div>
            </div>
            {feedbacks.map((item, index) => (
                <FeedbackItem key={index} item={item} />
            ))}
        </Card>
    </Container>
  )
}

export default MyFeedbacks