import {
  Button,
  Card,
  Col,
  Form,
  Modal,
  Row,
} from "@themesberg/react-bootstrap";
import React, { useState } from "react";
import "./Feedback.css";

function FeedbackItem({ item }) {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <Card
        className="bg-light mb-3"
        style={{ cursor: "pointer", overflowX: "hidden" }}
      >
        <Card.Body>
          <Row>
            <Col sm={5}>
              <Card.Title>Feedback ID: {item.id}</Card.Title>
              <Button className="mt-3" variant="success" onClick={handleShow}>
                Details
              </Button>
            </Col>
            <Col sm={7}>
              <div className="d-flex flex-column align-items-start">
                <div className="d-flex w-100 p-1">
                  <div>
                    <p style={{ margin: "0px", fontWeight: "700" }}>
                      Created on :{" "}
                      {new Date(item.createdAt).toString().slice(0, 15)}
                    </p>
                  </div>
                  <div
                    className={`tag border rounded px-1 ${
                      item.isResolved ? "resolved" : "pending"
                    }`}
                  >
                    {item.isResolved ? "Resolved" : "Pending"}
                  </div>
                </div>
                <div className="d-flex w-100 p-1">
                  <div>
                    <p style={{ margin: "0px", fontWeight: "700" }}>
                      Resolved on :{" "}
                      {item.isResolved
                        ? `${new Date(item.resolved_date)
                            .toString()
                            .slice(0, 15)}`
                        : "NA"}
                    </p>
                  </div>
                </div>
                <div className="d-flex w-100 p-1">
                  <div>
                    <p style={{ margin: "0px", fontWeight: "700" }}>
                      Resolved By : {item.isResolved ? item.resolved_by : "NA"}
                    </p>
                  </div>
                </div>
              </div>
            </Col>
          </Row>
        </Card.Body>
      </Card>
      <Modal show={show} onHide={handleClose} size="lg">
        <Modal.Header closeButton>
          <Modal.Title className="text-center">Feedback Or Query</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form.Group>
            <Form.Label>Feedback Or Query :</Form.Label>
            <Form.Control
              className="mr-2"
              name="feedback_data"
              value={item.feedback_data}
              id="feedback_data"
              as="textarea"
              rows={5}
              disabled
            />
          </Form.Group>
          <Form.Group>
            <Form.Label>Reply :</Form.Label>
            <Form.Control
              className="mr-2"
              name="feedback_data"
              value={item.isResolved ? item.feedback_reply : " "}
              id="feedback_data"
              as="textarea"
              rows={5}
              disabled
            />
          </Form.Group>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default FeedbackItem;
